#map {
  margin: auto;
  width: 95%;
  height: 75vh;
}

@media screen and (max-width: 600px) {
  #map {
    width: 85%;
    height: 73vh;
  }
}

#container {
  position: relative;
  top: 70px;
}

 

#send-button {
  position: absolute;
  width: 5rem;
  left: 0;
  right: 0;
  top: 85%;
  margin: 0 auto;
  z-index: 800;
}

.appear {
  animation: slide-in-bottom 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
@keyframes slide-in-bottom {
  0% {
    transform: translateY(1000px);
    opacity: 0;
  }
  85% {
    transform: translateY(-10px);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.disappear {
  animation: slide-out-bottom 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
  pointer-events: none;
}
@keyframes slide-out-bottom {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  15% {
    transform: translateY(-10px);
    opacity: 1;
  }
  100% {
    transform: translateY(1000px);
    opacity: 0;
  }
}

.info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}
.info h4 {
  margin: 0 0 5px;
  font-size: 1.8vw;
  color: #777;
}

.info h6 {
  font-size: 1.5vw;
}

@media screen and (max-width: 1000px) {
  .info h4 {
    font-size: 3.8vw;
  }
  .info h6 {
    font-size: 3vw;
  }
}
.legend {
  line-height: 18px;
  color: #555;
}
.legend i {
  width: 18px;
  height: 17px;
  float: left;
  margin-right: 8px;
  opacity: 0.7;
}

.hide {
  display: none;
}

