/* header */

.logo {
  width: 70px;
  height: 70px;
}

.links {
  font-size: 1em;
}

.links a:hover {
  border-bottom: 3px solid rgb(255, 255, 255);
}

@media screen and (max-width: 600px) {
  .links {
    font-size: 0.8em;
  }
  .logo {
    width: 60px;
    height: 60px;
  }
}

/* misc */
.anchor {
  display: block;
  position: relative;
  top: -70px;
  visibility: hidden;
}
